    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root {
    /* Brand Colors */
    --color-primary: #162636;
    /* Deep Navy */
    --color-secondary: #f44336;
    /* Accent Red */
    --color-tertiary: #6e66a9;
    /* Muted Purple */

    /* Neutrals */
    --color-bg: #f5f6f8;
    /* Light Neutral */
    --color-surface: #ffffff;
    /* Cards / Panels */
    --color-text-dark: #1a1a1a;
    --color-text-light: #ffffff;
    --color-border: #d7d7e0;

    /* Typography */
    --font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    --font-size-base: 16px;
    --font-size-sm: 14px;
    --font-size-lg: 20px;
    --line-height-base: 1.6;
    --font-weight-normal: 400;
    --font-weight-medium: 500;
    --font-weight-bold: 600;

    /* Spacing Scale */
    --space-xs: 4px;
    --space-sm: 8px;
    --space-md: 16px;
    --space-lg: 24px;
    --space-xl: 32px;

    /* Radius + Shadows */
    --radius-sm: 4px;
    --radius-md: 8px;
    --radius-lg: 12px;

    --shadow-sm: 0 1px 3px rgba(0, 0, 0, 0.12);
    --shadow-md: 0 4px 8px rgba(0, 0, 0, 0.10);
    --shadow-lg: 0 8px 20px rgba(0, 0, 0, 0.08);

    /* Transition */
    --transition-fast: 150ms ease;
    --transition-normal: 250ms ease;
}

html{
    display: flex;
    flex-direction: column;
    min-height: 100%;
}

body {
    font-family: var(--font-family);
    background-color: var(--color-bg);
    color: var(--color-text-dark);
    line-height: var(--line-height-base);
    font-size: var(--font-size-base);
    display: flex;
    flex-direction: column;
    flex: 1;
}

.container{
    display: flex;
    flex: 1;
    flex-direction: column;
}

header{
    background-color: var(--color-primary);
    color: var(--color-text-light);
    padding: var(--space-md);
    display: flex;
    align-items: center;
}

header .logo{
    height: 50px;
    width: auto;
}

header .title{
    font-size: 1.5rem;
    margin-left: 10px;
    margin-right: auto;
}

header .navbar{
    display: flex;
    gap: var(--space-md);
}

header .navbar button{
    background-color: var(--color-tertiary);
    color: var(--color-text-light);
    border: none;
    padding: var(--space-sm) var(--space-md);
    cursor: pointer;
    border-radius: var(--radius-sm);
    font-size: var(--font-size-base);
    transition: var(--transition-normal);
}

header .navbar button:hover{
    background-color: var(--color-secondary);
}

header .navbar button:active{
    transform: scale(0.98);
}

.content{
    display: flex;
    flex: 1;
}

footer{
    background-color: var(--color-primary);
    color: var(--color-text-light);
    text-align: center;
    padding: var(--space-md);
}

#home-page {
    display: flex;
    flex: 1;
    justify-content: center;
    align-items: center;
    min-height: 80vh;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-size: cover;
    background-position: center;
}

#home-page .card {
    background: var(--color-surface);
    padding: var(--space-lg) var(--space-xl);
    border-radius: var(--radius-md);
    text-align: center;
    box-shadow: var(--shadow-md);
}

#home-page .card h2 {
    font-weight: var(--font-weight-bold);
    margin-bottom: var(--space-sm);
}

#home-page .card p {
    margin-bottom: var(--space-md);
}

#home-page .card button {
    padding: var(--space-sm) var(--space-lg);
    border-radius: var(--radius-sm);
    background: var(--color-tertiary);
    color: var(--color-text-light);
    font-weight: var(--font-weight-medium);
    transition: var(--transition-fast);
    border: none;
}

#home-page .card button:hover {
    background: #4151ff;
}

#home-page .card button:active {
    transform: scale(0.97);
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,SAAS;IACT,UAAU;IACV,sBAAsB;AAC1B;;AAEA;IACI,iBAAiB;IACjB,wBAAwB;IACxB,cAAc;IACd,0BAA0B;IAC1B,eAAe;IACf,yBAAyB;IACzB,iBAAiB;;IAEjB,aAAa;IACb,mBAAmB;IACnB,kBAAkB;IAClB,wBAAwB;IACxB,mBAAmB;IACnB,0BAA0B;IAC1B,2BAA2B;IAC3B,uBAAuB;;IAEvB,eAAe;IACf,8DAA8D;IAC9D,sBAAsB;IACtB,oBAAoB;IACpB,oBAAoB;IACpB,uBAAuB;IACvB,yBAAyB;IACzB,yBAAyB;IACzB,uBAAuB;;IAEvB,kBAAkB;IAClB,eAAe;IACf,eAAe;IACf,gBAAgB;IAChB,gBAAgB;IAChB,gBAAgB;;IAEhB,qBAAqB;IACrB,gBAAgB;IAChB,gBAAgB;IAChB,iBAAiB;;IAEjB,0CAA0C;IAC1C,0CAA0C;IAC1C,2CAA2C;;IAE3C,eAAe;IACf,6BAA6B;IAC7B,+BAA+B;AACnC;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,gBAAgB;AACpB;;AAEA;IACI,+BAA+B;IAC/B,iCAAiC;IACjC,6BAA6B;IAC7B,oCAAoC;IACpC,gCAAgC;IAChC,aAAa;IACb,sBAAsB;IACtB,OAAO;AACX;;AAEA;IACI,aAAa;IACb,OAAO;IACP,sBAAsB;AAC1B;;AAEA;IACI,sCAAsC;IACtC,8BAA8B;IAC9B,wBAAwB;IACxB,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,YAAY;IACZ,WAAW;AACf;;AAEA;IACI,iBAAiB;IACjB,iBAAiB;IACjB,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,oBAAoB;AACxB;;AAEA;IACI,uCAAuC;IACvC,8BAA8B;IAC9B,YAAY;IACZ,wCAAwC;IACxC,eAAe;IACf,+BAA+B;IAC/B,gCAAgC;IAChC,oCAAoC;AACxC;;AAEA;IACI,wCAAwC;AAC5C;;AAEA;IACI,sBAAsB;AAC1B;;AAEA;IACI,aAAa;IACb,OAAO;AACX;;AAEA;IACI,sCAAsC;IACtC,8BAA8B;IAC9B,kBAAkB;IAClB,wBAAwB;AAC5B;;AAEA;IACI,aAAa;IACb,OAAO;IACP,uBAAuB;IACvB,mBAAmB;IACnB,gBAAgB;IAChB,yDAAkD;IAClD,sBAAsB;IACtB,2BAA2B;AAC/B;;AAEA;IACI,gCAAgC;IAChC,wCAAwC;IACxC,+BAA+B;IAC/B,kBAAkB;IAClB,4BAA4B;AAChC;;AAEA;IACI,oCAAoC;IACpC,8BAA8B;AAClC;;AAEA;IACI,8BAA8B;AAClC;;AAEA;IACI,wCAAwC;IACxC,+BAA+B;IAC/B,iCAAiC;IACjC,8BAA8B;IAC9B,sCAAsC;IACtC,kCAAkC;IAClC,YAAY;AAChB;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,sBAAsB;AAC1B","sourcesContent":["*{\n    margin: 0;\n    padding: 0;\n    box-sizing: border-box;\n}\n\n:root {\n    /* Brand Colors */\n    --color-primary: #162636;\n    /* Deep Navy */\n    --color-secondary: #f44336;\n    /* Accent Red */\n    --color-tertiary: #6e66a9;\n    /* Muted Purple */\n\n    /* Neutrals */\n    --color-bg: